<template>
	<view class="container">
		<view class="title">
			<view class="left">
				<text>负责人员：{{servicer}}</text>
				<text>公司名称：{{companyName}}</text>
				<text>联系电话：{{phoneNo}}</text>
			</view>
			<view class="right">
				<image class="right-img" src="../../static/images/event_center_icon.png" mode="aspectFit"></image>
			</view>
		</view>
		<view class="content">
			<view class="tab-bar">
				<text class="tab-bar-item" :class="{ on : currentIndex === 1 }" @click="switchTabBar(1)">服务内容</text>
				<text class="tab-bar-item" :class="{ on : currentIndex === 2 }" @click="switchTabBar(2)">服务进度</text>
				<text class="tab-bar-item" :class="{ on : currentIndex === 3 }" @click="switchTabBar(3)">关联资产</text>
				<text class="tab-bar-item" :class="{ on : currentIndex === 4 }" @click="switchTabBar(4)">处理记录</text>
				<text class="tab-bar-item" :class="{ on : currentIndex === 5 }" @click="switchTabBar(5)">知识库</text>
			</view>
			<view class="divider"></view>
			<!--内容主体-->
			<view class="service-content" v-if="currentIndex === 1">
				<view class="assert-title">
					<image class='assert-title-image' src='../../static/images/service_detail.png'></image>
					<text class='assert-title-text'>内容详情</text>
				</view>
				<view class="assert-content-group">
					<view class="assert-content-item">
						<view class="key">
							标题
						</view>
						<view class="value">
							: {{ faultInfoTitle }}
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							报修人
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							公司名称
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							联系电话
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							故障等级
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							来源
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							故障时间
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							影响度
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							服务目录
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							权重
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							最近更新
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							服务态度
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							工作质量
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<view class="assert-content-item">
						<view class="key">
							客评详情
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<!-- 分割线 -->
					<view class="divider"></view>
					<view class="service_fault_group">
						<view class="service_fault_content">
							故障内容:
						</view>
						<view class="service_fault_content">
							冯生
						</view>
					</view>
					<!-- 分割线 -->
					<view class="divider"></view>
					<view class="assert-content-item">
						<view class="key">
							语音描述
						</view>
						<view class="value">
							: 冯生
						</view>
					</view>
					<!-- 分割线 -->
					<view class="divider"></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import serviceCode from '@/apis/index.js';
	export default {
		onLoad(option) {
			const item = JSON.parse(decodeURIComponent(option.item));
			console.log("item", item)
			this.servicer = item.personName;
			this.companyName = item.companyNameRepair;
			this.phoneNo = item.personMobile;
			this.faultId = item.faultId;
			this.serviceId = item.serviceid;
			this.faultIp = item.equipmentId;
			this.personId = item.personId;
			this.processId = item.processId;
			this.perId = item.perId;
			this.getServiceDetail()
		},
		data() {
			return {
				currentIndex: 1,
				servicer: '',
				companyName: '',
				phoneNo: '',
				faultId: '',
				serviceId: '',
				faultIp: '',
				personId: '',
				processId: '',
				perId: '',
				faultInfoTitle: ''
			};
		},
		methods: {
			switchTabBar(index) {
				this.currentIndex = index;
			},
			getServiceDetail() {
								console.log("this", this)
				const param = {
					"faultInfo.faultId": this.faultId,
					"faultInfo.serviceid": this.serviceId,
					"faultInfo.equipmentId": this.faultIp,
					"userId": uni.getStorageSync('userInfo').id,
					"mouleId": uni.getStorageSync("appRole"),
					"processId": this.processId,
					"processPerId": this.perId
				};
				this.$request({
					url: serviceCode['GET_SERVICE_DETAIL_YI'],
					method: 'POST',
					data: param,
					success: (res) => {
						this.faultInfoTitle = res.listFaultInfo[0].title
						console.log("this.faultInfo", this.faultInfoTitle)
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		padding: 15rpx;
		box-sizing: border-box;

		.title {
			display: flex;
			width: 100%;
			height: 180rpx;

			.left {
				height: 160rpx;
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
			}

			.right {
				height: 160rpx;

				.right-img {
					width: 227rpx;
					height: 100%;
				}
			}
		}

		.content {
			flex: 1;

			.divider {
				width: 100%;
				height: 10rpx;
				background-color: #49B2F2;
			}

			.tab-bar {
				width: 100%;
				display: flex;
				justify-content: space-between;

				.tab-bar-item {
					flex: 1;
					text-align: center;
					padding: 10rpx 0;
				}

				.on {
					background-color: #49B2F2;
					color: #FFF;
				}
			}

			.service-content {

				.assert-title {
					height: 64rpx;
					display: flex;
					align-items: center;
					margin: 10rpx 0;
					border-bottom: 2rpx solid black;

					&-image {
						width: 40rpx;
						height: 40rpx;
					}

					&-text {
						font-size: 35rpx;
						margin-left: 10rpx;
					}
				}

				.assert-content-group {
					.assert-content-item {
						display: flex;
						align-items: center;
						height: 88rpx;
						font-size: 25rpx;
						border-bottom: 0.5rpx solid #F0F1EC;

						.key {
							width: 100rpx;
							height: 88rpx;
							line-height: 88rpx;
							text-align: justify;
							font-size: 25rpx;

							&::after {
								content: " ";
								display: inline-block;
								width: 100%;
							}
						}

						.value {
							font-size: 25rpx;
						}
					}
				}

				.service_fault_group {
					display: flex;
					align-items: center;
					flex-wrap: wrap;
					height: 176rpx;

					.service_fault_content {
						width: 100%;
						font-size: 25rpx;
					}
				}
			}
		}
	}
</style>
